@import "_partial/_config";

/* background */
.recruitIndex {
	background: url(/img/img01.jpg) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}
/* -----------------------------------------------------------------------
## recruit ###############################################################
----------------------------------------------------------------------- */
.recruitSec {
	padding-top: 16px;
	.visualBox_copy {
		text-align: center;
	}
	&_navi {
		margin-top: 32px;
		text-align: center;
		a {
			border: 1px solid $color_line;
		}
	}
	&_table {
		width: 100%;
		margin-top: 16px;
		table-layout: fixed;
		border-top: 1px solid $color_line;
		border-left: 1px solid $color_line;
		@include fontsize(14);
		th,td {
			padding: 16px;
			border-bottom: 1px solid $color_line;
			border-right: 1px solid $color_line;
		}
		th {
			width: 150px;
		}
	}
}
@include mediaQuery(sp){
	.recruitSec {
		.visualBox_copy {
			text-align: left;
			br {
				display: none;
			}
		}
		.visualBox_text {
			line-height: 1.5;
			@include fontsize(14);
		}
		&_navi {
			a {
				display: block;
				margin-top: 16px;
				border: 1px solid $color_line;
			}
		}
		&_table {
			display: block;
			th,td {
				display: block;
			}
			th {
				width: auto;
				padding: 8px 16px;
				background: $color_bg_light;
				color: $color_text_dark;
			}
		}
	}
}