@charset "utf-8";

/* -----------------------------------------------------------------------
## variables #############################################################
----------------------------------------------------------------------- */
/* colors */
$color_bg: #281f1c;
$color_bg_light: #fff;
$color_text: #fff;
$color_text_dark: #281f1c;
$color_line: #fff;
$color_line_dark: #281f1c;
$color_link: #fff;
$color_mime: #e5001a;
$color_breakin: #f9be02;
$color_magic: #008fd8;
$color_jaggling: #00994c;
$color_dall: #9e9e9f;

/* easings */
$easeOutBack: cubic-bezier(0.175, 0.885, 0.32, 1.275);
$easeOutQuint: cubic-bezier(0.23, 1, 0.32, 1);
$easeOutElastic: cubic-bezier(0.23, 1, 0.32, 1);

/* breakpoints */
$breakpoints: (
  'sp': 'screen and (max-width: 768px)',
  'pc': 'screen and (min-width: 769px)',
) !default;

/* -----------------------------------------------------------------------
## mixins ################################################################
----------------------------------------------------------------------- */
@mixin fontsize($size: 16, $base: 16){
	font-size: ($size / $base) * 1rem;
}

@mixin imgTxtBox($imgWidth, $txtWidth){
	.imgBox {
		float: left;
		width: $imgWidth;
	}
	.txtBox {
		float: right;
		width: $txtWidth;
	}
}
@mixin txtImgBox($txtWidth, $imgWidth){
	.imgBox {
		float: right;
		width: $imgWidth;
	}
	.txtBox {
		float: left;
		width: $txtWidth;
	}
}
@mixin mediaQuery($breakpoint) {
    @if map-has-key($breakpoints, $breakpoint) {
        @media #{inspect(map-get($breakpoints, $breakpoint))} {
            @content;
        }
    } @else {
        @media screen and (max-width: $breakpoint) {
            @content;
        }
    }
}